@import url('https://fonts.googleapis.com/css2?family=Galindo&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Galindo', cursive;
}
body{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(to right, #FFDDFF,white);  
    /* background-color: pink; */
}
.container{
    position: relative;
    max-width: 430px;
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    border-radius:2px 2px 0px grey;
    box-shadow: 0 4px 30px rgba(0,0,0,0.1);
    backdrop-filter: blur(5px);
    
}
.container .form{
padding: 30px;
}
.container .form .title{
    position: relative;
    font-size: 27px;
    font-weight: 600;
}

.form .title::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 3px;
    width: 30px;
    background-color: slateblue;
    border-radius: 25px;
}
.form .input-field{
 position: relative;
    height: 50px;
    width: 100%;
    margin-top: 30px;
}
.input-field input[type=text],input[type=password],input[type=email]{
    height: 100%;
    width: 100%;
    border: none;
    padding: 0 35px;
    outline: none;
    background-color: transparent;
    border-bottom: 2px solid grey;
    border-top: 2px solid transparent;
    font-size: 16px;
}
.input-field input:is(:focus, :valid){
    border-bottom-color:red;
    }

.input-field i{
    position: absolute;
    top: 50%;
    transform:translateY(-50%) ;
    color: black;
    font-size: 23px;
}


.input-field i.email{
    left: 0;
}
.input-field i.icon{
left: 0;
} 
.input-field i.hide{
    right: 0;
}
.input-field i.user{
    left: 0;
}
.input-field i.eye{
    right: 0;
}
.button{
    width: 100%;
    height: 100%;
    padding: 7px;
letter-spacing: 4px;
border: none;
font-size: 24px;    
transition: 0.5;
background-color: pink;
   box-shadow: 0 0 10px pink,  0 0 40px pink, 0 0 80px pink;
   color: grey;
}
.button:hover{
transition-delay:1s ;
color: rgb(178, 135, 142);
background-color: rgb(227, 191, 197);
   box-shadow: 0 0 10px rgb(227, 191, 197),  0 0 40px rgb(227, 191, 197), 0 0 80px rgb(227, 191, 197);
}

.input-field i.nim{
    left: 0;
}

